@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  src: url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  src: url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  background-color: #ffffff;
  font-style: normal;
}
@media (min-width: 1200px) {
  .page {
    background-color: #f2f2f2;
  }
}
.page .page__body {
  margin: 0 auto;
  min-height: 100vh;
  width: auto;
  min-width: 320px;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .page .page__body {
    filter: drop-shadow(0 5px 15px rgba(0, 1, 1, 0.2));
  }
}
.main-header {
  position: relative;
  color: #ffffff;
}
.main-container {
  width: 100%;
  flex-grow: 1;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  width: 100%;
  background-color: #81b3d2;
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 56px;
  background-color: #ffffff;
}
.main-nav__logo {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  width: 185px;
  height: 96px;
  z-index: 2;
}
.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 18px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 22px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 4px;
  width: 23px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
.main-nav--opened {
  background-color: #81b3d2;
}
.main-nav--opened .main-nav__logo {
  z-index: -1;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__logo {
    z-index: 2;
  }
}
.main-nav--opened .main-nav__toggle {
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  height: 4px;
  width: 26px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  visibility: visible;
  position: static;
}
.main-nav--nojs .site__item {
  display: block;
}
@media (max-width: 767px) {
  .main-nav--closed .site__item {
    display: none;
  }
}
.site {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .site {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    background-color: #ffffff;
  }
}
.site::before {
  content: "";
  grid-column: 3/4;
  grid-row: 1/3;
}
.site__item {
  position: relative;
  width: 100%;
  min-height: 56px;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
@media (max-width: 767px) {
  .site__item:last-child::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 320px) {
  .site__item:hover {
    background-color: #669ec0;
  }
  .site__item:active {
    background-color: #5496bd;
  }
  .site__item:disabled {
    color: #e5e5e5;
  }
}
@media (min-width: 768px) {
  .site__item {
    border: none;
  }
  .site__item:hover {
    background-color: #ffffff;
  }
  .site__item:nth-of-type(1) {
    grid-column: 1/2;
  }
  .site__item:nth-of-type(2) {
    grid-column: 2/3;
  }
  .site__item:nth-of-type(3) {
    grid-column: 4/5;
  }
  .site__item:nth-of-type(4) {
    grid-column: 5/6;
  }
}
.site__item-link {
  position: relative;
  display: block;
  text-align: center;
  padding: 17px 77px 12px;
  box-sizing: border-box;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}
@media (min-width: 320px) {
  .site__item-link:active {
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .site__item-link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    padding: 17px 22px 18px;
  }
  .site__item-link--current {
    color: #766357;
  }
  .site__item-link:hover {
    color: #81b3d2;
  }
  .site__item-link:active {
    color: #766357;
  }
  .site__item-link:disabled {
    color: #e5e5e5;
  }
}
@media (min-width: 1200px) {
  .site__item-link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    padding: 17px 68px 18px;
  }
  .site__item-link--current {
    color: #766357;
  }
  .site__item-link:hover {
    color: #81b3d2;
  }
  .site__item-link:active {
    color: #766357;
  }
  .site__item-link:disabled {
    color: #e5e5e5;
  }
}
